import React, { Component } from 'react';
import {
  Container, Form,
  FormGroup, Label, Input,
  Button, FormText 
} from 'reactstrap';
import '../component/ForReport/Report.css';

import Header from '../component/Header'
import Footer from '../component/Footer'
import BreadCrumbForCreateReport from '../component/ForReport/BreadCrumbForCreateReport'

class CreateReportPage extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      dropdownOpen: false
    };
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  render() {
    return (
      <div>
        <Header />
        <BreadCrumbForCreateReport />
        <div className="site-card-wrapper">
          <div
          style={{
            backgroundColor: 'white',
            backgroundSize: "cover",
          }}
          >
          <Container className="App">
            <h2>Bo co kim tra nh k</h2>
            <Form>
              <legend>Cng trnh</legend>
              <FormGroup>
                <Label for="congtrinh">Cng trnh</Label>
                <Input type="text" name="congtrinh" id="congtrinh" placeholder="Cng trnh A" />
              </FormGroup>

              <legend>Thng tin bo co</legend>
              <FormGroup>
                <Label for="thongtin1">Thng tin 1</Label>
                <Input type="text" name="thongtin1" id="thongtin1" placeholder="Ghi ch" />
              </FormGroup>
              <FormGroup>
                <Label for="thongtin2">Thng tin 2</Label>
                <Input type="textarea" name="thongtin2" id="thongtin2" placeholder="Ghi ch" />
              </FormGroup>
              <FormGroup>
                <Label for="thongtin3">Thng tin 3</Label>
                <Input type="select" name="thongtin3" id="thongtin3">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </Input>
              </FormGroup>
              <FormGroup>
                <Label for="file1">Tp nh km</Label>
                <Input type="file" name="file1" id="file1" />
                <FormText color="muted">
                  Ghi ch v tp ti ln
                </FormText>
              </FormGroup>
              <FormGroup tag="fieldset">
                <legend>nh gi</legend>
                <FormGroup check>
                  <Label check>
                    <Input type="radio" name="radio1" />{' '}
                    Rt km
                  </Label>
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input type="radio" name="radio1" />{' '}
                    Km
                  </Label>
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input type="radio" name="radio1" />{' '}
                    Va
                  </Label>
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input type="radio" name="radio1" />{' '}
                    Tt
                  </Label>
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input type="radio" name="radio1" />{' '}
                    Xut xc
                  </Label>
                </FormGroup>
              </FormGroup>
              <legend>Thng tin ph</legend>
              <FormGroup check>
                <Label check>
                  <Input type="checkbox" />{' '}
                  Lnh vc 1
                </Label>
              </FormGroup>
              <FormGroup check>
                <Label check>
                  <Input type="checkbox" />{' '}
                  Lnh vc 2
                </Label>
              </FormGroup>
              <FormGroup check>
                <Label check>
                  <Input type="checkbox" />{' '}
                  Lnh vc 3
                </Label>
              </FormGroup>
              <FormGroup>
                <Input type="textarea" name="thongtinphu" id="thongtinphu" placeholder="Ghi ch" />
              </FormGroup>
              <Button>Hon thin bo co</Button>
            </Form>
          </Container>
        </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default CreateReportPage;