import React, { Component } from 'react';

// const { Meta } = Card;

class ConstructionInfo extends Component {

  constructor(props) {
    super(props);

    const tabList = [
      {
        key: 'Ngi ph trch',
        tab: 'Ngi ph trch',
      },
      {
        key: 'Thi gian d nh',
        tab: 'Thi gian d nh',
      },
      {
        key: 'S k s tham gia',
        tab: 'S k s tham gia',
      },
    ];
    
    const contentList = {
      'Ngi ph trch': <p>{this.props.work.leader}</p>,
      'Thi gian d nh': <p>{this.props.work.start} - {this.props.work.end}: {this.props.work.duration} ngy</p>,
      'S k s tham gia': <p>{this.props.work.members} thnh vin</p>,
    }

    this.state = { 
      work: this.props.work, 
      tabList: tabList, 
      contentList: contentList,
      key: 'Ngi ph trch'
    };
  }

  onTabChange = (key, type) => {
    console.log(key, type);
    this.setState({ [type]: key });
  };

  render() {
    return (
      <div id="constructionInfo">
        {/* <Card size="small" title={this.state.work.name}>
          <p>Ngi ph trch: {this.state.work.leader}</p>
          <p>{this.state.work.start} - {this.state.work.end}</p>
          <p>Thi gian d nh: {this.state.work.duration}</p>
          <p>S k s tham gia: {this.state.work.members}</p>
        </Card> */}

        {/* <Card
          style={{ width: 260 }} cover = {<img alt="construction_site" style={{height: "180px"}} src={this.state.work.imgUrl} />}
          tabList={this.state.tabList}
          // activeTabKey={this.state.noTitleKey}
          onTabChange={key => {
            this.onTabChange(key, 'key');
          }}
        >
          {this.state.contentList[this.state.key]}
        </Card> */}
        <img src={this.state.work.imgUrl} alt="construction_site_img" style={{width: "100%"}}/>
        <div style={{"font-size": "12px", "margin-top": "4px", "margin-left": "7px" }}>
          <p>Ngi ph trch: {this.state.work.leader}</p>
          <p>{this.state.work.start} - {this.state.work.end}</p>
          <p>Thi gian d nh: {this.state.work.duration}</p>
          <p>S k s tham gia: {this.state.work.members}</p>
        </div>
        
      </div>
    );
  }
}

export default ConstructionInfo;